import React, { Component} from 'react';
import { Card, CardImg, CardText, CardBody, Breadcrumb, BreadcrumbItem, Label, Button, Modal, ModalHeader, ModalBody} from 'reactstrap';
import { Link } from 'react-router-dom';
import { Loading } from './LoadingComponent';
import { Control, LocalForm, Errors } from 'react-redux-form';
import { baseUrl } from '../shared/baseUrl';
import { FadeTransform, Fade, Stagger } from 'react-animation-components';

const required = val => val && val.length;
const maxLength = len => val => !val || (val.length <= len);
const minLength = len => val => val && (val.length >= len);



function RenderCampsite({campsite}) {
    return (
      <div className="col-md-5 m-1">
      <FadeTransform
          in
          transformProps={{
              exitTransform: 'scale(0.5) translateY(-50%)'
          }}>
          <Card>
              <CardImg top src={baseUrl + campsite.image} alt={campsite.name} />
              <CardBody>
                  <CardText>{campsite.description}</CardText>
              </CardBody>
          </Card>
      </FadeTransform>
  </div>
    )

  }

  function RenderComments({comments, postComment, campsiteId}) {
    if(comments){
        return (
            <div className="col-md-5 m-1">
 <h4>Comments</h4>
                <Stagger in>
                    {comments.map(comment => {
                        return (
                            <Fade in key={comment.id}>
                                <div>
                                    <p>{comment.text}<br />
                                        -- {comment.author}, {new Intl.DateTimeFormat('en-US', { year: 'numeric', month: 'short', day: '2-digit'}).format(new Date(Date.parse(comment.date)))}
                                    </p>
                                </div>
                            </Fade>
                        );
                    })}
                </Stagger>
 <CommentForm campsiteId={campsiteId} postComment={postComment} />             </div>
        )
    }
return <div />
  }


  

  class  CommentForm extends Component {
    constructor(props) {
        super(props);
        this.state = {
          modal: false,
          rating: '',
          author: '',
          text: ''
        };
    
        this.toggle = this.toggle.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
      }
    
      toggle() {
        this.setState({
          modal: !this.state.modal
        });
      }
  
      handleSubmit(values) {
          console.log("Values are: " + JSON.stringify(values));
          alert("The values are: " + JSON.stringify(values));
          console.log("the reating is " + values.rating + " the authos is " + values.author)
          this.props.postComment(this.props.campsiteId, values.rating, values.author, values.text); 
      }

      render() {
        return (
          <div>    
            <Button outline onClick={this.toggle}><i className="fa fa-pencil"></i>{' '}Submit Comment</Button>
            <Modal isOpen={this.state.modal} toggle={this.toggle} >
              <ModalHeader toggle={this.toggle}>Submit Comment</ModalHeader>
              <ModalBody>
               <LocalForm  onSubmit={values => this.handleSubmit(values)}>
               <div className='group'>
                  <Label htmlFor='rating'>Your Rating: </Label>
                  <Control.select model='.rating' id='rating'  name='rating' placeholder='Your Name' className='form-control mb-2'>
                      <option>Choose rating</option>
                      <option>1</option>
                      <option>2</option>
                      <option>3</option>
                      <option>4</option>
                      <option>5</option>
                  </Control.select>
  
               </div>
               <div className='group'>
                  <Label htmlFor='name'>Your Name: </Label>
                  <Control.text model='.author'
                   id='author' name='author' 
                   placeholder='Your Name'
                    className='form-control mb-2'
                    validators={{
                      required, 
                      minLength: minLength(2),
                      maxLength: maxLength(15)
                  }}
                    />
                       <Errors 
                                       className="text-danger"
                                       model=".author"
                                       show="touched"
                                       component="div"
                                       messages={{
                                           required: 'Required',
                                           minLength: 'Must be at least 2 characters',
                                           maxLength: 'Must be 15 characters or less'
                                       }}
                                   />
               </div>
               <div className='group'>
                  <Label htmlFor='comment'>Comment: </Label>
                  <Control.textarea rows='6' model='.text' id='text' name='text' placeholder='Your Comment' className='form-control mb-2'/>
               </div>        
               <Button type='submit' color='primary' onClick={this.toggle}>Do Something</Button>{' '}
               </LocalForm>
              </ModalBody>
  
            </Modal>
          </div>
        );
      }


  }


  function CampsiteInfo(props) {


    if (props.isLoading) {
      return (
          <div className="container">
              <div className="row">
                  <Loading />
              </div>
          </div>
      );
  }
  if (props.errMess) {
      return (
          <div className="container">
              <div className="row">
                  <div className="col">
                      <h4>{props.errMess}</h4>
                  </div>
              </div>
          </div>
      );
  }
  

    if (props.campsite) {
        return (
            <div className="container">
              <div className="row">
                <div className="col">
                <Breadcrumb>
                        <BreadcrumbItem><Link to="/directory">Directory</Link></BreadcrumbItem>
        <BreadcrumbItem active>{props.campsite.name}</BreadcrumbItem>
                    </Breadcrumb>
                    <h2>{props.campsite.name}</h2>
                    <hr />
                </div>
            </div>              
                <div className="row">
                    <RenderCampsite campsite={props.campsite} />
                    <RenderComments 
                        comments={props.comments}
                        postComment={props.postComment}    
                        campsiteId={props.campsite.id}
                    />
                    </div>
            </div>
        );
    }
    return <div />;
}

export default CampsiteInfo